import React from "react";
import { createContext } from "react";
import { useState } from "react";

export let ServiceContext = createContext(null);
export default function ServicesContextProvider(props) {
  let [num, setNum] = useState(0);
  // For Increase Number
  let incNum = () => {
    if (num < 100) {
      setNum(Number(num) + 1);
    }
  };
  // For Decrease Number
  let decNum = () => {
    if (num > 0) {
      setNum(num - 1);
    }
  };
  // For Handling Number
  let handleChange = (e) => {
    setNum(e.target.value);
  };
  let myData = {
    name: "T-shirt",
    price: 9.999,
    imgURl: "Images/he4717-12.jpg",
    count: num,
  };

  return (
    // For send all data between all components
    <ServiceContext.Provider
      value={{ myData, incNum, decNum, handleChange, num }}
    >
      {props.children}
    </ServiceContext.Provider>
  );
}
