import React, { useContext } from "react";
import { ServiceContext } from "../../Services/Services";
import styles from "./Navbar.module.scss";
import { Link } from "react-router-dom";
import { useState } from "react";
import { Card, Button, Drawer } from "antd";

export default function Navbar() {
  let { myData, num, decNum } = useContext(ServiceContext);
  const [visible, setVisible] = useState(false);

  const showDrawer = (type) => {
    setVisible(true);
  };

  const closeDrawer = () => {
    setVisible(false);
  };
  return (
    <>
      <div className="container ml-5">
        <Drawer
          title={"My Cart"}
          width={400}
          onClose={closeDrawer}
          visible={visible}
          bodyStyle={{ paddingBottom: 80 }}
          footer={
            <div style={{ textAlign: "right" }}>
              <Button onClick={closeDrawer} style={{ marginRight: 8 }}>
                Cancel
              </Button>
              <Button onClick={closeDrawer} type="primary">
                Submit
              </Button>
            </div>
          }
        >
          {/*  */}
          <h3>Cart Summary</h3>
          {num > 0 ? (
            <Card>
              <div className="d-flex">
                <img
                  className="w-25 h-100"
                  src={myData.imgURl}
                  alt="yeshtery"
                />
                <div className="d-flex flex-column">
                  <p>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit.
                    Fuga, ipsa.
                  </p>
                  <span>Type: {myData.name} </span>
                  <span>Quantity: {num}</span>
                  <div className="d-flex justify-content-between">
                    <span className={`${styles.priceCol} fw-bold`}>
                      Price: {myData.price} LE
                    </span>
                    <button onClick={decNum} className={`${styles.btnMin} btn`}>
                      Remove
                    </button>
                  </div>
                </div>
              </div>
            </Card>
          ) : (
            ""
          )}
          {num > 0 ? (
            <>
              <h4 className="d-flex justify-content-center my-3">
                Total: {num * 9.999} LE
              </h4>
              <div className="d-flex justify-content-evenly">
                <button className={`${styles.btnMin} btn`}>Review Cart</button>
                <button className={`${styles.addToCart} btn`}>
                  Complete Checkout
                </button>
              </div>
            </>
          ) : (
            ""
          )}
        </Drawer>
      </div>
      <nav className={`navbar navbar-expand-lg ${styles.navBg}`}>
        <div className="container w-75">
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon" />
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <img
                  className="mt-1 mx-3"
                  src="Images/Group 769.png"
                  alt="yeshtery"
                />
              </li>
              <li className="nav-item d-flex mt-2">
                <p className="px-5">
                  &lt; Valaentine's Day Offers! But Two Get One Free
                  <span className="mx-2 text-decoration-underline ">
                    Shop Now
                  </span>{" "}
                  &gt;
                </p>
              </li>
            </ul>
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <li className="nav-item d-flex mt-3 mx-2">
                <i class="fa-solid fa-phone-volume mt-1 mx-2"></i>
                <p>Contact Us</p>
              </li>
              <li className="nav-item d-flex mt-3 mx-2">
                <i class="fa-solid fa-cart-shopping mt-1 mx-2"></i>
                <p>Track Order</p>
              </li>
              <li className="nav-item d-flex mt-3 mx-3">
                <i class="fa-solid fa-location-dot mt-1 mx-2"></i>
                <p>Find A Store</p>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      <nav className="navbar navbar-expand-lg">
        <div className="container w-75 ">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0 w-100 d-flex justify-content-between align-items-center">
            <li className="nav-item">
              <div className="position-relative" role="search">
                <i
                  class={`fa-solid fa-magnifying-glass ${styles.searchIcon}`}
                ></i>
                <input
                  className="rounded-5 me-2"
                  type="search"
                  placeholder=" Search"
                  aria-label="Search"
                />
              </div>
            </li>
            <li className="nav-item">
              <img className="" src="Images/Group 745.png" alt="yeshtery" />
            </li>
            <div className="d-flex mt-3">
              <div className="d-flex mx-5">
                <Button type="text" onClick={() => showDrawer("login")}>
                  <div className="d-flex position-relative">
                    <i class="mt-1 mx-2 fa-solid fa-bag-shopping"></i>
                    <p>Cart</p>
                    <span
                      className={`${styles.cartNum} d-flex justify-content-center align-items-center`}
                    >
                      {num}
                    </span>
                  </div>
                </Button>{" "}
                <li className="d-flex mx-3">
                  <i class="mt-1 mx-2 fa-solid fa-heart"></i>
                  <p>Wishlist</p>
                </li>
                <li className="d-flex mx-3">
                  <i class="mt-1 mx-2 fa-solid fa-user"></i>
                  <p>Login</p>
                </li>
              </div>
            </div>
          </ul>
        </div>
      </nav>
      <nav className="navbar navbar-expand-lg bg-black">
        <div className="container w-75">
          <ul className="navbar-nav mb-2 mb-lg-0 w-100 d-flex justify-content-between">
            <li className="nav-item me-5">
              <Link className="nav-link text-white" to="">
                Men
              </Link>
            </li>
            <li className="nav-item me-5">
              <Link className="nav-link text-white" to="women">
                Women
              </Link>
            </li>
            <li className="nav-item me-5">
              <Link className="nav-link text-white" to="unisex">
                Unisex
              </Link>
            </li>
            <li className="nav-item me-5">
              <Link className="nav-link text-white" to="kids">
                Kids
              </Link>
            </li>
            <li className="nav-item me-5">
              <Link className="nav-link text-white" to="bestseller">
                Best Seller
              </Link>
            </li>
            <li className="nav-item me-5">
              <Link className="nav-link text-white" to="newarrival">
                New Arrival
              </Link>
            </li>
            <li className="nav-item me-5">
              <Link className="nav-link text-white" to="offers">
                Offers
              </Link>
            </li>
          </ul>
        </div>
      </nav>
    </>
  );
}
